input {
  border: 1px solid rgb(225, 224, 224);
  border-radius: 5px;
  box-shadow: inset 0 0 10px 5px rgba(226, 226, 223, 0.3);
  width: 300px;
  height: 35px;
}

button {
  border: 1px solid rgb(225, 224, 224);
  border-radius: 5px;
  box-shadow: inset 0 0 10px 5px rgba(226, 226, 223, 0.3);
  height: 35px;
}

.search-form {
  background-color: rgb(190, 192, 196);
  text-align: center;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

img {
  width: 280px;
  height: 200px;
}

.info {
  display: flex;
  justify-content: center;
  gap: 5px;
}

.span {
  display: flex;

  justify-content: center;
  color: #9b9b9b;
}

.photo-card {
  border-radius: 4px;
  box-shadow: inset 0 0 10px 5px rgba(196, 196, 191, 0.3);
  gap: 10px;
}
